.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: 
    radial-gradient(farthest-side,#666666 94%,#0000) top/4px 4px no-repeat,
    conic-gradient(#0000 30%,#666666);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 4px),#000 0);
  animation: l13 1s infinite linear;
}
@keyframes l13{ 
  100%{transform: rotate(1turn)}
}

.loader2 {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: 
    radial-gradient(farthest-side,#E96E07 94%,#0000) top/10px 10px no-repeat,
    conic-gradient(#0000 30%,#E96E07);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 10px),#000 0);
  animation: l14 1s infinite linear;
}
@keyframes l14{ 
  100%{transform: rotate(1turn)}
}

#text1 {
	background: linear-gradient(to bottom, #D3AF37, #000000);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}